import BbwzDriver from "../BbwzDriver";
import BbwzConstDefine from "../data/BbwzConstDefine";
import BbwzBaseView from "./BbwzBaseView";

/**
 * 游戏层 大赢家
 */
export default class BbwzBigWinnerView extends BbwzBaseView {
    private bigSpine: sp.Skeleton = null;
    private head: cc.Sprite = null;
    private head_kuang: cc.Sprite = null;
    private money: cc.Label = null;
    private nameLbl: cc.Label = null;

    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.bigSpine = cc.find("content/spine", this.node).getComponent(sp.Skeleton);
        this.head = cc.find("content/mask/sprite_head", this.node).getComponent(cc.Sprite);
        this.head_kuang = cc.find("content/head_kuang", this.node).getComponent(cc.Sprite);
        this.money = cc.find("content/bigwinMoney", this.node).getComponent(cc.Label);
        this.nameLbl = cc.find("content/name", this.node).getComponent(cc.Label);
    }

    public updateUI(big_winner: any) {
        this.clear();
        if (big_winner && big_winner.nickname !== "") {
            this.head.spriteFrame = Global.Toolkit.getLocalHeadSf(big_winner.headimg);
            BbwzDriver.instance.loadVipHeadKuang(this.head_kuang, big_winner.a_box);
            this.money.string = Global.Toolkit.formatPointStr(big_winner.hitPoint, true, true);
            this.nameLbl.string = big_winner.nickname;

            this.bigSpine.setAnimation(0, "idle", false);
            Game.Component.scheduleOnce(() => {
                this.head.node.active = true;
                this.head_kuang.node.active = true;
                this.money.node.active = true;
                this.nameLbl.node.active = true;
            }, 0.5);
            Global.Audio.playBundleSound(BbwzConstDefine.GAME_ID,BbwzConstDefine.SOUND_WIN, true);
        }
    }

    private clear() {
        this.head.node.active = false;
        this.head_kuang.node.active = false;
        this.money.node.active = false;
        this.nameLbl.node.active = false;
        this.bigSpine.clearTracks();
    }

    public clearByRound(){
        this.active = false;
    }
}
